body {
     font-family: 'Myriad Apple Text';
}
#mainNav {
  min-height: 56px;
  background-color: rgba(28, 28, 28, 0.8);
  backdrop-filter: blur(2px) grayscale(0.4);

  .navbar-toggler {
    font-size: 80%;
    padding: 0.75rem;
    color: #9e140e;
    border: 1px solid #9e140e;

    &:focus {
      outline: none;
    }
  }

  .navbar-brand {
    color: #161616;
    font-weight: 700;
    padding: .9rem 0;
  }

  .navbar-nav .nav-item {
    &:hover {
      color: fade(white, 80%);
      outline: none;
      background-color: transparent;
    }

    &:active, &:focus {
      outline: none;
      background-color: transparent;
    }
  }
}

@media (min-width: 992px) {
  #mainNav {
    padding-top: 0;
    padding-bottom: 0;
    border-bottom: none;
    background-color: transparent;
    transition: background-color 0.3s ease-in-out;

    .navbar-brand {
      padding: .5rem 0;
      color: rgba(255, 255, 255, 0.5);
    }

    .nav-link {
      transition: none;
      padding: 2rem 1.5rem;
      color: rgba(255, 255, 255, 0.5);

      &:hover {
        color: rgba(255, 255, 255, 0.75);
      }

      &:active {
        color: white;
      }
    }

    &.navbar-shrink {
        background-color: rgba(28, 28, 28, 0.8);

      .navbar-brand {
        color: white;
      }

      .nav-link {
        color: white;
        padding: 1.5rem 1.5rem 1.25rem;
        border-bottom: 0.25rem solid transparent;

        &:hover {
          color: #9e140e;
        }

        &:active {
          color: #467370;
        }

        &.active {
          color: #9e140e;
          outline: none;
          border-bottom: 0.25rem solid #9e140e;
        }
      }
    }
  }
}

.masthead {
    position: relative;
    width: 100%;
    height: auto;
    min-height: 35rem;
    padding: 15rem 0;
    background: linear-gradient(to bottom, rgba(22, 22, 22, 0.3) 0%, rgba(22, 22, 22, 0.7) 75%, #161616 100%), url("../../imgs/home/bg-masthead.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-size: cover;

    h1 {
        font-family: 'Myriad Apple Text';
        font-size: 2.5rem;
        line-height: 2.5rem;
        letter-spacing: 0.8rem;
        background: -webkit-linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.3));
        -webkit-text-fill-color: transparent;
        -webkit-background-clip: text;
    }

  h2 {
    max-width: 20rem;
    font-size: 1rem;
  }
}

@media (min-width: 768px) {
  .masthead h1 {
    font-size: 4rem;
    line-height: 4rem;
  }
}

@media (min-width: 992px) {
  .masthead {
    height: 100vh;
    padding: 0;

    h1 {
      font-size: 6rem;
      line-height: 6rem;
      letter-spacing: 0.8rem;
    }

    h2 {
      max-width: 30rem;
      font-size: 1.5rem;
    }
  }
}

.btn {
  box-shadow: 0 0.1875rem 0.1875rem 0 rgba(0, 0, 0, 0.1) !important;
  padding: 1.25rem 2rem;
  font-family: 'Myriad Apple Text';
  font-size: 80%;
  text-transform: uppercase;
  letter-spacing: .15rem;
  border: 0;
}

