.premium-section {
  padding-top: 10rem;
  background: linear-gradient(to bottom,rgba(22, 22, 22, 0.3) 0%, rgba(22, 22, 22, 0.2) 75%, rgba(22, 22, 22, 0.8) 100%);

  p {
    margin-bottom: 5rem;
  }
}

.features-section {
  padding: 10rem 0;

  .featured-text {
    padding: 2rem;
  }

  .feature-text {
    padding: 3rem;
    font-size: 90%;
  }
}

@media (min-width: 992px) {
  .features-section .featured-text {
    padding: 0 0 0 2rem;
    border-left: 0.5rem solid #9e140e;
  }
}

@media (min-width: 992px) {
  .features-section .feature-text {
    padding: 5rem;

    hr {
      border-color: #9e140e;
      border-width: .25rem;
      width: 30%;
    }
  }
}

.lw-search-section {
  padding: 10rem 0;
  background: linear-gradient(to bottom, rgba(22, 22, 22, 0.1) 0%, rgba(22, 22, 22, 0.5) 75%, #161616 100%), url("../../imgs/home/people-2561578_1920.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;

   input, select {
    box-shadow: 0 0.1875rem 0.1875rem 0 rgba(0, 0, 0, 0.1) !important;
    padding: 1.25rem 2rem;
    height: auto;
    font-family: 'Myriad Apple Text';
    font-size: 80%;
    text-transform: uppercase;
    letter-spacing: 0.15rem;
    border: 0;
  }
  .custom-select {
          -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
  }
}

.card.lw-transparent-card {
    background-color: rgba(0, 0, 0, 0.6);
    border: 0;
    backdrop-filter: blur(3px) grayscale(0.4);
    padding-top: 16px;
}
.lw-feature-items {
    line-height: 2em;
}

.contact-section {
  padding: 5rem 0 0;

  .card {
    border: 0;
    border-bottom: 0.25rem solid #9e140e;

    h4 {
      font-size: 0.8rem;
      font-family: 'Myriad Apple Text';
      text-transform: uppercase;
      letter-spacing: 0.15rem;
    }

    hr {
      border-color: #9e140e;
      border-width: 0.25rem;
      width: 3rem;
    }
  }

  .social {
    margin-top: 5rem;

    a {
      text-align: center;
      height: 3rem;
      width: 3rem;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 100%;
      line-height: 3rem;
      color: rgba(255, 255, 255, 0.3);

      &:hover {
        color: rgba(255, 255, 255, 0.5);
      }

      &:active {
        color: #fff;
      }
    }
  }
}

footer {
  padding: 5rem 0;
}

.lw-logo-img {
    height: 80px;
}